@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&family=Rubik:wght@300;700&family=Kaushan+Script&display=swap);
@font-face{src:url(/static/media/Elianto.7e57dbb0.otf);font-family:elianto}*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}html{width:100%;height:100%;font-family:'Roboto', sans-serif;font-size:62.5%}@media only screen and (max-width: 75em){html{font-size:56.25%}}@media only screen and (max-width: 56.25em){html{font-size:50%}}@media only screen and (min-width: 112.5em){html{font-size:75%}}body{width:100%;height:100%;position:relative}body a,body a:hover{text-decoration:none !important}body #root{position:relative;width:100%;height:100%}

.home{width:100%;min-height:100%;background:#333646;display:flex;flex-direction:column;align-items:center}

.landing{width:100%;height:100vh;color:white}.landing__name{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:white;font-size:6rem;font-family:'Kaushan Script', cursive;margin-left:7vw;padding-top:25vh;padding-bottom:2rem;text-shadow:-0.3rem 0.3rem 0.7rem rgba(0,0,0,0.7);transition:all 0.3s ease}.landing__name:hover{text-shadow:-0.5rem 0.5rem 1.3rem rgba(0,0,0,0.9);transform:scale(1.02)}.landing__typer{margin-left:7vw;font-family:'Courier New', Courier, monospace;padding-bottom:3rem;font-size:1.5vw;font-weight:700;white-space:nowrap;letter-spacing:0.16vw;display:inline-block;box-sizing:border-box}.landing__typer--line1{overflow:hidden;border-right:4px solid #00c3ff;-webkit-animation:blink 0.5s step-end 7 both,typing 2s steps(30, end) 2s backwards;animation:blink 0.5s step-end 7 both,typing 2s steps(30, end) 2s backwards}.landing__typer--line2{overflow:hidden;border-right:4px solid #00c3ff;-webkit-animation:blink 0.5s step-end 4s 3 both,typing 2s steps(30, end) 4s backwards;animation:blink 0.5s step-end 4s 3 both,typing 2s steps(30, end) 4s backwards}.landing__typer--line3{overflow:hidden;border-right:4px solid #00c3ff;-webkit-animation:blink 0.5s step-end 6s 5 both,typing 2.5s steps(30, end) 7s backwards,blink 0.5s step-end 8s infinite;animation:blink 0.5s step-end 6s 5 both,typing 2.5s steps(30, end) 7s backwards,blink 0.5s step-end 8s infinite}.landing__btn{height:100%;position:relative;z-index:1;background:white;padding:0.7em 3rem;transition:all 0.3s ease;border:0.15rem solid white;box-shadow:0 0.3rem 0.4rem rgba(0,0,0,0.32),0 0.3rem 0.3rem rgba(0,0,0,0.54);cursor:pointer}.landing__btn::before{position:absolute;content:'';top:0;right:0;bottom:0;left:0;background:linear-gradient(to right, #7f00ff, #e100ff);z-index:-1;transition:opacity 0.3s ease;opacity:0}.landing__btn--text{font-size:1.6rem;background:linear-gradient(to right, #7f00ff, #e100ff);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.landing__btn:hover{transform:scale(1.1);box-shadow:0 1rem 2.8rem rgba(0,0,0,0.55),0 1rem 1rem rgba(0,0,0,0.52)}.landing__btn:hover::before{opacity:1}.landing__btn:hover .landing__btn--text{font-weight:bold;background:white;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.landing__btn-grp{margin-left:7vw;width:25%;display:flex;justify-content:space-between}@-webkit-keyframes typing{from{width:0%}to{width:100%}}@keyframes typing{from{width:0%}to{width:100%}}@-webkit-keyframes blink{from,to{border-color:transparent}50%{border-color:#00c3ff}}@keyframes blink{from,to{border-color:transparent}50%{border-color:#00c3ff}}

.experience{height:120vh;background:white;color:#333;margin-top:-27vh;padding-top:27vh;-webkit-clip-path:polygon(0 25vh, 100% 0, 100% calc(100% - 75vh), 0 100%);clip-path:polygon(0 25vh, 100% 0, 100% calc(100% - 25vh), 0 100%);display:flex;flex-direction:column;align-items:center}.experience__heading{display:inline-block;font-family:elianto, sans-serif;text-align:center;font-size:6rem;background:linear-gradient(to right, #fc00ff, #00dbde);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.experience--wrapper{width:calc(100% - 10rem);-webkit-filter:drop-shadow(0 1rem 2rem rgba(0,0,0,0.75));filter:drop-shadow(0 1rem 2rem rgba(0,0,0,0.75))}


